.counter {
  &::before {
    counter-increment: number;
    content: "- " "№"counter(number) " -";
    @extend%font-regular;
    position: absolute;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    bottom: 17px;
    left: 0;
    right: 0;

    @media (min-width: $tablet-width) {
      bottom: 23px;
    }
  }

  &--dark {
    color: $color-black;
  }
}
