.offer {
  background-color: $color-white;
  text-align: center;
}

.offer__title {
  @include font-bold(21px);
  padding-bottom: 16px;

  @media (min-width: $tablet-width) {
    @include font-bold(30px);
    padding-bottom: 32px;
  }
}

.offer__description {
  @extend%font-regular;
  color: $color-gray;
  padding-bottom: 32px;
  padding-top: 3px;

  @media (max-width: 767px) {
    & br {
      content: "";

      &::after {
        content: "\00a0";
      }
    }
  }

  @media (min-width: $tablet-width) {
    padding: 0;
    padding-bottom: 41px;
  }

  @media (min-width: $desktop-width) {
    padding-bottom: 49px;
  }
}

.offer__button {
  text-align: center;
  margin-top: 26px;
  padding: 17px 49px 15px;

  @media (min-width: $tablet-width) {
    margin-top: 41px;
    min-width: 458px;
    padding: 15px 49px 17px;
  }

  @media (min-width: $desktop-width) {
    margin-top: 48px;
    padding: 15px 53px 17px;
  }
}
