@charset "UTF-8";
@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
}
@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
}
.copyright__link,
.copyright__icon, .copyright__title, .modal__description, .feedback__wrapper, .offer__description, .recommend__description, .counter::before, .advantages-card__description, .page__text {
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  text-transform: uppercase;
  margin: 0;
}

body {
  background-color: #ffffff;
  max-width: 320px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  min-height: 100%;
}
@media (min-width: 320px) {
  body {
    max-width: 768px;
  }
}
@media (min-width: 768px) {
  body {
    max-width: 1200px;
  }
}
@media (min-width: 1200px) {
  body {
    position: relative;
  }
  body::after {
    position: absolute;
    content: "";
    border-right: 15px solid #000101;
    border-image: linear-gradient(to right, rgba(0, 1, 1, 0.2), #e7e5e5) 0 100%;
    right: -15px;
    height: 100%;
  }
  body::before {
    position: absolute;
    content: "";
    border-right: 15px solid #000101;
    border-image: linear-gradient(to left, rgba(0, 1, 1, 0.2), #e7e5e5) 0 100%;
    left: -15px;
    height: 100%;
  }
}

main {
  flex-grow: 2;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0, 0, 0, 0);
  overflow: hidden;
}

input,
button,
textarea {
  outline: 0;
  font-family: "PT Sans", "Arial", "Helvetica", sans-serif;
}

input:focus + span {
  background-color: #d5d5d5;
}

.page-header {
  position: relative;
  min-height: 56px;
  background-color: #ffffff;
}

.page-header__toggle {
  position: absolute;
  border: none;
  background-color: #ffffff;
  padding: 0;
  width: 44px;
  height: 44px;
  top: 6px;
  right: 4px;
  cursor: pointer;
}

.page-header__logo {
  position: absolute;
  margin-left: auto;
  margin-right: auto;
  top: 0;
  left: -2px;
  right: 0;
  text-align: center;
  max-width: 185px;
  z-index: 6;
}

.page-header__navigation {
