.navigation__item {
  padding-top: 18px;
  padding-bottom: 13px;
  text-align: center;
  position: relative;
}
.navigation__item:not(:first-child)::before {
  content: "";
  position: absolute;
  border-bottom: 1px solid #ffffff;
  opacity: 0.2;
  left: 0;
  right: 0;
  top: 0;
}
.navigation__item:not(:last-child)::after {
  content: "";
  position: absolute;
  border-bottom: 1px solid #000000;
  opacity: 0.2;
  left: 0;
  right: 0;
  bottom: 0;
}
.navigation__item:last-child::after {
  content: "";
  position: absolute;
  height: 7px;
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.2));
  left: 0;
  right: 0;
  bottom: 0;
}
.navigation__item:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .navigation__item {
    min-width: 153px;
  }
  .navigation__item::after, .navigation__item::before {
    display: none;
  }
}
