.toggle--opened {
  z-index: 3;
}
.toggle--opened::before {
  content: "";
  position: absolute;
  top: 11px;
  left: 5px;
  width: 23px;
  height: 4px;
  background-color: #81b3d2;
  box-shadow: 0 9px 0 0 #81b3d2, 0 18px 0 0 #81b3d2;
}
.toggle--opened:hover {
  opacity: 0.6;
}
.toggle--opened:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .toggle--opened {
    display: none;
  }
}

.toggle--closed {
  z-index: 10;
  background-color: #81b3d2;
}
@media (min-width: 768px) {
  .toggle--closed {
    display: none;
  }
}
.toggle--closed::before {
  content: "";
  position: absolute;
  width: 4px;
  height: 28px;
  background-color: #ffffff;
  transform: rotate(45deg);
  top: 8px;
  left: 15px;
}
.toggle--closed::after {
  content: "";
  position: absolute;
  width: 4px;
  height: 28px;
  background-color: #ffffff;
  transform: rotate(-45deg);
  top: 8px;
  left: 15px;
}
.toggle--closed:hover {
  opacity: 0.6;
}
.toggle--closed:active {
  opacity: 0.3;
}

