.gallery {
  margin-top: -1px;
}

.gallery__list {
  list-style-type: none;
  padding-left: 20px;
  padding-right: 20px;
  margin: 0;
  background-color: $color-white;


  @media (min-width: $tablet-width) {
    display: flex;
    flex-wrap: wrap;
    padding-left: 60px;
    padding-right: 60px;
  }

  @media (min-width: $desktop-width) {
    padding: 0;
  }
}

.gallery__item {
  padding-top: 25px;
  padding-bottom: 24px;
  position: relative;

  &::after {
    display: block;
    position: absolute;
    content: "";
    border-top: 1px solid $catalog-card-line;
    right: -20px;
    left: -30px;
    bottom: -1px;
  }

  @media (min-width: $tablet-width) {
    padding: 0;

    &::after {
      display: none;
    }
  }

  @media (min-width: $desktop-width) {
    position: relative;
  }

  &:first-child {
    width: 100%;

    &::before {
      display: block;
      position: absolute;
      content: "";
      border-top: 1px solid $catalog-card-line;
      right: -20px;
      left: -30px;
      top: 0;
    }

    @media (min-width: $tablet-width) {
      padding: 0;

      &::before {
        display: none;
      }
    }
  }
}

.gallery__image {
  display: block;
  object-fit: cover;
  min-width: 100%;

  @media (min-width: $tablet-width) {
    max-height: 267px;
    min-width: 324px;
  }

  @media (min-width: $desktop-width) {
    max-height: 267px;
    width: 100%;
  }

  &--main {
    @media (min-width: $tablet-width) {
      min-height: 400px;
      min-width: 100%;
    }
  }
}

.gallery__description {
  margin-top: 15px;

  @media (min-width: $tablet-width) {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 22px 20px 13px 20px;
    background-color: $fordFieldsColor;
    margin-top: 0;
  }

  @media (min-width: $desktop-width) {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: $color-black-opacity;
    color: $color-white;
    margin-top: 0;
    flex-wrap: wrap;
  }

  &--text-edit {
    @media (min-width: $desktop-width) {
      padding-bottom: 17px;
      padding-top: 18px;
    }
  }
}

.gallery__container {
  padding-bottom: 8px;

  @media (min-width: $tablet-width) {
    padding-bottom: 0;
  }
}

.gallery__author {
  font-weight: 400;
  font-size: 14px;
  line-height: 26px;
  margin: 0;
  margin-top: -1px;
}

.gallery__social {
  @media (min-width: $tablet-width) {
    margin-top: -10px;
  }

  &--text-edit {
    @media (min-width: $desktop-width) {
      margin-top: 0;
    }
  }
}

.gallery__description--alternative {
  @media (min-width: $tablet-width) {
    background-color: $color-gray-very-light;
  }

  @media (min-width: $desktop-width) {
    background-color: $color-black-opacity;
  }
}

.gallery__title {
  font-weight: 700;
  font-size: 14px;
  line-height: 21px;
  text-transform: uppercase;
  margin: 0;
}

.gallery__like {
  padding: 0;
  border: none;
  background-color: transparent;
  cursor: pointer;
  width: 23px;
  height: 21px;
}

.gallery__icon {
  @media (min-width: $tablet-width) {
    padding-left: 5px;
    padding-top: 2px;
  }

  & path {
    opacity: 0.3;

    @media (min-width: $desktop-width) {
      fill: $color-white;
      opacity: 1;
    }
  }
}

.gallery__likes {
  font-weight: 700;
  font-size: 14px;
  line-height: 21px;
  margin: 0;
  margin-left: 10px;
}
